@import url(https://fonts.googleapis.com/css2?family=Shrikhand&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Cabin&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lato:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Cabin&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Cabin&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Shrikhand&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Cabin&display=swap);
* {
	box-sizing: border-box;
}

.grid {
	display: grid;
	grid-template-columns: 1fr 1fr;
}

.intro {
	background-image: url("https://res.cloudinary.com/dusr8fbuo/image/upload/v1607197647/scott-webb-KpuWtiszFQw-unsplash_mqa1qx.jpg");
	background-size: cover;
	background-repeat: no-repeat;
	height: 100vh;
	width: 100%;
}

.content-links {
	grid-column: 2/3;
	background-color: rgb(240, 239, 219);
	height: 100vh;
	display: grid;
	width: 100%;
}

.greeting {
	margin-top: 60px;
	font-size: 150px;
	letter-spacing: -13px;
	background: -webkit-linear-gradient(rgb(197, 232, 183), rgb(66, 52, 62));
	-webkit-background-clip: text;
	-webkit-text-fill-color: transparent;
	font-family: "Shrikhand", cursive;
	text-align: center;
}

.statement {
	font-family: "Cabin", sans-serif;
	font-size: 27px;
	text-align: justify;
	text-align: center;
	padding: 0px 20px 20px 20px;
	line-height: 150%;
	text-align: center;
	letter-spacing: 1px;
}

.skills-list {
	font-family: "Cabin", sans-serif;
	font-size: 22px;
	text-align: justify;
	text-align: center;
	color: #fff;
	padding: 0px 20px 20px 20px;
	line-height: 150%;
	text-align: center;
	letter-spacing: 3px;
}

.title {
	margin-top: 20px;
	font-size: 65px;
	letter-spacing: -6px;
	color: rgb(98, 87, 85);
	font-family: "Shrikhand", cursive;
	text-align: center;
}

.hover-content {
	width: 100%;
	padding: 0 100px;
	font-family: "Cabin", sans-serif;
	font-size: 18px;
	justify-content: center;
	text-align: center;
	transition: 0.9s ease;
	opacity: 0;
}

.mobile-img {
	display: none;
}

#about {
	font-size: 16px;
}
.section {
	transition: 0.1s;
	-webkit-tap-highlight-color: transparent;
}
.section:hover {
	/* background-color: rgba(125, 134, 117, 0.1); */
	background-color: rgb(244, 240, 238);
	border: 3px solid black;
	border-style: double;
}

.section:hover .title {
	transition: all 0.5s;
	background: -webkit-linear-gradient(rgb(98, 87, 85), rgb(244, 202, 145));
	-webkit-background-clip: text;
	-webkit-text-fill-color: transparent;
}
.section:hover .hover-content {
	opacity: 1;
}

.social-icons {
	display: flex;
	justify-content: center;
}

.social-icons i {
	/* padding: 10px; */
	padding-left: 10px;
	padding-right: 10px;
	transition: all 0.8s ease-in-out;
	margin-top: 10px;
}

.social-icons i:hover {
	transform: rotateY(360deg);
	color: rgb(136, 176, 120);
}

.social-icons a {
	color: black;
	text-decoration: none; /* no underline */
}

.navbar-fixed {
	display: none !important;
	/* display: none; */
}

.mobile-text {
	display: none;
}

@media screen and (max-width: 1100px) and (min-width: 150px) {
	.intro {
		height: 130vh;
		width: 100%;
	}

	.content-links {
		height: 130vh;
		width: 100%;
	}

	.navbar-fixed {
		background-color: white;
		display: block !important;
		visibility: visible;
	}

	#display-nav-mobile {
		display: block !important;
	}
	.test {
		display: block;
	}

	.greeting {
		margin-top: 60px;
		font-size: 100px;
		letter-spacing: -8px;
	}

	.skills-list {
		margin-top: 100px;
	}

	.hover-content {
		width: 100%;
		padding: 0 70px;
		font-family: "Cabin", sans-serif;
		font-size: 18px;
		justify-content: center;
		text-align: right;
		opacity: 0;
	}
}

@media screen and (max-width: 868px) {
	div .grid {
		display: flex;
		flex-direction: column;
		width: 100%;
	}
	div .section {
		display: flex;
		flex-direction: column;
		width: 100%;
	}

	.greeting {
		margin-top: 60px;
		font-size: 80px;
		letter-spacing: -7px;
	}

	.statement {
		font-size: 27px;
		/* text-align: justify; */
		text-align: left;
		padding: 30px;
		line-height: 120%;
		color: #fff;
	}

	.skills-list {
		margin-top: 15%;
	}

	.title {
		display: none;
	}

	.hover-content {
		width: 100%;
		/* padding: 0 100px; */
		padding: 0px;
		font-size: 21px;
		justify-content: center;
		text-align: left;
		opacity: 1;
		line-height: 120%;
		height: 100vh;
		width: 100%;
	}

	#about {
		font-size: 21px;
	}

	.mobile-img {
		display: flex;
		margin: 0 auto;
		position: relative;
		margin-top: 0;
		margin-bottom: 25%;
	}

	.mobile-img img {
		margin: 0 auto;
		width: 100%;
	}

	.mobile-txt-container {
		display: block;
		position: absolute;
		bottom: 50%;
		right: 10px;
		font-size: 55px;
		color: rgb(248, 205, 150);
		font-family: "Cabin", sans-serif;
		/* background-color: white; */
		opacity: 0.9;
		font-style: italic;
		font-weight: 700;
		text-shadow: 1px 1px 2px black;
	}

	.mobile-text {
		padding: 11px;
	}

	.section {
		background-color: rgb(244, 240, 238);
		border: none;
	}

	.section:hover {
		/* background-color: rgba(125, 134, 117, 0.1); */
		border: none;
	}

	.section-text {
		padding: 28px;
		font-size: 20px;
		/* text-align: justify; */
		/* line-height: 100%; */
		width: 100%;
		font-family: "Lato", sans-serif;
	}

	.social-icons {
		margin-top: 25%;
	}
}

.brand {
	font-family: "Cabin", sans-serif;
	font-size: 35px;
	background: -webkit-linear-gradient(rgb(98, 87, 85), rgb(244, 202, 145));
	-webkit-background-clip: text;
	-webkit-text-fill-color: transparent;
}
.ml-auto {
	font-family: "Cabin", sans-serif;
	font-size: 20px;
	color: black;
	/* background: -webkit-linear-gradient(rgb(98, 87, 85), rgb(244, 202, 145));
	-webkit-background-clip: text;
	-webkit-text-fill-color: transparent; */
}

.nav-link {
	letter-spacing: -2px;
}
.nav-link:hover {
	text-decoration: underline;
}

body {
	font-family: "Raleway", sans-serif;
	background-color: rgb(240, 239, 219);
}

.page-title {
	font-family: "Shrikhand", cursive;
	text-align: center;
	font-size: 45px;
	letter-spacing: -4px;
	background: -webkit-linear-gradient(rgb(197, 232, 183), rgb(66, 52, 62));
	-webkit-background-clip: text;
	-webkit-text-fill-color: transparent;
	text-transform: capitalize;
}

div .page-title {
	margin-top: 45px;
	margin-bottom: 55px;
}

.proj-container {
	display: flex;
	flex-direction: column;
	margin-top: 100px;
}

.proj-card {
	box-shadow: 0px 10px 30px rgb(244, 240, 238);
	margin-left: 20px;
	margin-right: 20px;
	transition: none;
	height: 80vh;
}

.proj-card:hover {
	transition: none;
}

div .image {
	text-align: center;
}
.proj-info {
	color: black;
	font-family: "Cabin", sans-serif;
	size: 35px;
	padding-left: 25px;
	padding-right: 25px;
}

.proj-titl {
	text-align: center;
	color: rgb(248, 205, 150);
	text-shadow: 10px 10px 10px #eee;
	letter-spacing: 2px;
	margin-top: 15px;
}
.proj-desc {
	font-size: 14px;
	color: black;
	padding: 10px;
	line-height: 150%;
	text-align: center;
	letter-spacing: 1px;
}

.proj-btns {
	display: flex;
	justify-content: center;
}

.proj-btns i {
	padding: 20px;
}

