.footer {
	position: absolute;
	right: 0;
	bottom: 0;
	left: 0;
	color: black;
	background-color: transparent;
	text-align: center;
	letter-spacing: 2px;
	font-style: italic;
	font-size: 18px;
}

@media screen and (max-width: 1000px) and (min-width: 150px) {
	.footer {
		color: black;
		position: fixed;
		bottom: 0;
		width: 100%;
		background-color: transparent;
		text-align: center;
		letter-spacing: 2px;
		font-style: italic;
		font-size: 18px;
	}
}

